import React, { Component } from "react";
import { Redirect } from 'react-router-dom';
import './style.css';
import axios from "axios";


class Vip extends Component{

    constructor(props) {
        super(props);
        this.state = {
            login:true,
            fetchFinish: false
        }
    }
    render() {
        if (this.state.login) {
            if (this.state.fetchFinish) {
                return <div className={"vip"}>vip</div>
            } else {
                return <div className={"vip"}>。。。。。。。</div>
            }
        } else {
            return <Redirect to = '/' />
        }
    }

    componentDidMount() {
        axios.get('http://www.dell-lee.com/react/api/isLogin.json',
            {withCredentials: true
        })
        .then(res => {
            const isLogin = res.data.data.login;
            this.setState({
                login:isLogin,
                fetchFinish: true
            })
            // console.log(login)
        })
    }
}

export default Vip;